* {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
  }
  li {
    list-style-type: none;
  }
  a {
    text-decoration: none;
    color: white;
  }
  nav {
    display: flex;
    height: 6vh;
    border-radius: 2em;
    background: rgb(99, 113, 201);
    justify-content: space-around;
    position: sticky;
    top: 0;
    align-items: center;
  }
  nav .header {
    font-size: 24px;
    font-weight: bold;
    text-transform: uppercase;
  }
  nav .header:hover {
    cursor: pointer;
  }
  nav .navbar-links {
    display: flex;
    justify-content: space-around;
    width: 70%;
    cursor: pointer;
  }
  nav .navbar-links a {
    font-size: 17px;
    font-weight: normal;
    cursor: pointer;
    text-transform: capitalize;
  }
  nav .navbar-links a:hover {
    cursor: pointer;
    color: black;
  }
  nav .burger-menu {
    font-size: 24px;
    color: white;
    cursor: pointer;
    display: none;
  }
  .nav-active {
    color: black;
  }
  
  @media all and (max-width: 777px) {
    nav .burger-menu {
      display: block;
    }
    nav .navbar-links {
      background-color: rgb(68, 79, 234);
      display: block;
      width: 50%;
      position: absolute;
      top: 100%;
      left: 0;
      padding: 0px;
      transform: translate(-700px);
    }
    nav .navbar-links li {
      padding: 25px;
      border-top: 3px solid white;
    }
  }